input {
  margin: 10px;
  padding: 5px 10px;
  font-size: 20px
}

.form {
  font-size: 20px;
  display: flex;
  height: 100px;
}

.importantCheckbox {
  width: 14%;
  line-height: 100px;
}

.addTaskForm {
  width: 72%;
  line-height: 100px;
}

.addBtn{
  display: block;
  margin: auto 1%;
  width: 13%;
  padding: 3px 10px;
  text-transform: uppercase;
  font-size: 20px;
  background-color: #fff;
  border: 1px solid black;
  height: 50px;
  border-radius: 20px;
  cursor: pointer;
  transition: .3s ease-in-out;
}

.addBtn:hover {
  background-color: rgb(7, 137, 180);
  color: white;
}