.btnDone {
    width: 7%;
    height: 50px;
    background-color: green;
    border: none;
    cursor: pointer;
    border-left: 1px solid black;
    transition: .3s ease-in-out;
}

.btnDelete {
    height: 50px;
    width: 7%;
    background-color: red;
    border: none;
    cursor: pointer;
    border-left: 1px solid black;
}

.mainDiv {
    height: 50px;
    line-height: 50px;
    width: 72%;
}

.toDo {
    display: flex;
    height: 50px;
    width: 100%;
    border-bottom: 1px solid black;
}

.checkBox {
    display: block;
    margin: auto auto;
    width: 14%;
    height: 20px;
    line-height: 50px;
}

.done {
    display: inline;
}

.doneTask {
    height: 40px;
    font-size: 22px;
}

.deleteBtn {
    background-color: #fff;
    color: black;
    width: 50px;
    height: 50px;
    border-radius: 45px;
    border: 1px solid black;
    margin-left: 15px;
    cursor: pointer;
}
