body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

input {
  margin: 10px;
  padding: 5px 10px;
  font-size: 20px
}

.form {
  font-size: 20px;
  display: flex;
  height: 100px;
}

.importantCheckbox {
  width: 14%;
  line-height: 100px;
}

.addTaskForm {
  width: 72%;
  line-height: 100px;
}

.addBtn{
  display: block;
  margin: auto 1%;
  width: 13%;
  padding: 3px 10px;
  text-transform: uppercase;
  font-size: 20px;
  background-color: #fff;
  border: 1px solid black;
  height: 50px;
  border-radius: 20px;
  cursor: pointer;
  transition: .3s ease-in-out;
}

.addBtn:hover {
  background-color: rgb(7, 137, 180);
  color: white;
}
.btnDone {
    width: 7%;
    height: 50px;
    background-color: green;
    border: none;
    cursor: pointer;
    border-left: 1px solid black;
    transition: .3s ease-in-out;
}

.btnDelete {
    height: 50px;
    width: 7%;
    background-color: red;
    border: none;
    cursor: pointer;
    border-left: 1px solid black;
}

.mainDiv {
    height: 50px;
    line-height: 50px;
    width: 72%;
}

.toDo {
    display: flex;
    height: 50px;
    width: 100%;
    border-bottom: 1px solid black;
}

.checkBox {
    display: block;
    margin: auto auto;
    width: 14%;
    height: 20px;
    line-height: 50px;
}

.done {
    display: inline;
}

.doneTask {
    height: 40px;
    font-size: 22px;
}

.deleteBtn {
    background-color: #fff;
    color: black;
    width: 50px;
    height: 50px;
    border-radius: 45px;
    border: 1px solid black;
    margin-left: 15px;
    cursor: pointer;
}

.toDoTaskList {
    display: flex;
    height: 50px;
    width: 100%;
    border-bottom: 1px solid black;
    font-size: 18px;
    font-weight: bold;
}

.mainDiv {
    width: 72%;
    line-height: 50px;
}

.done {
    width: 7%;
    line-height: 50px;
}

.delete {
    width: 7%;
    line-height: 50px;
}

.important {
    line-height: 50px;
    width: 14%;
}
.App {
  text-align: center;
}

